import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import nameService from './services/persons'
import Notification from './components/Notification'

const App = () => {
  const [ persons, setPersons] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ newSearch, setNewSearch ] = useState('')
  const [ showAll, setShowAll ] = useState(true)
  const [notification, setNotification] = useState('')
  const [error, setError] = useState(false)

  useEffect(() => {
    nameService
      .getAll()
      .then(response => {
        setPersons(response.data)
      })
  }, [])

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleSearchChange = (event) => {
    if (event.target.value !== ''){
      setShowAll(false)
      setNewSearch(event.target.value)
    } else {
      if (event.target.value === ''){
        setShowAll(true)
      }
    }
    setNewSearch(event.target.value)
  }

  const deleteName = (event) => {
    event.preventDefault()
    console.log(event)
    const personId = (event.target.value)
    const currentPerson = (persons.find(person => person.id === personId))
    console.log(event.target.value)
    console.log(personId)
    if (window.confirm(`Delete ${event.target.value}?`) === true) {
    nameService
      .removeName(currentPerson.id)
      .then(response => {
        nameService
        .getAll()
        .then(response => {
          setPersons(response.data)
        })
      })
      .catch(error => {
        setError(true)
        setNotification(
          `${currentPerson.name} has already been deleted from the server`
        )
      })
    }
  }

  const addName = (event) => {
    event.preventDefault()
    const nameObject = {
      name: newName,
      number: newNumber,
      date: new Date().toISOString(),
    }
    if (checkName(nameObject.name)) {
      if(window.confirm(`${newName} is already registered. Want to update number?`) === true){
        const tempName = persons.find(person => person.name === nameObject.name)
        nameService
          .update(tempName.id, nameObject)
          .then(response => {
            nameService
            .getAll()
            .then(response => {
              setPersons(response.data)
              setError(false)
              setNotification(
                `${tempName.name}'s number has been updated.`
              )
              setTimeout(() => {
                setNotification(null)
              }, 5000)
            })
            setNewName('')
            setNewNumber('')
          })

      } else {
        setNewName('')
        setNewNumber('')
      }
      return setNewName('')
    }
    nameService
      .create(nameObject)
      .then(response => {
        nameService
          .getAll()
          .then(response => {
            setPersons(response.data)
          })
        setNewName('')
        setNewNumber('')
        setError(false)
        setNotification(
          `${nameObject.name} has been added to the Phonebook`
        )
        setTimeout(() => {
          setNotification(null)
        }, 5000)
      })
  }

  const personsToShow = showAll ? persons : persons.filter(person => person.name.match(newSearch))

  const checkName = (props) => {
    if (persons.find(person => person.name === props)) {
      return true
    }
  }

  return (
    <div>

      <h2>Phonebook</h2>
    <Notification error={error} notification={notification}/>

    <Filter
        newSearch={newSearch}
        handleSearchChange={handleSearchChange}
        />

      <h3>Add a Number</h3>

      <PersonForm
        addName={addName}
        newName={newName}
        handleNameChange={handleNameChange}
        newNumber={newNumber}
        handleNumberChange={handleNumberChange}
        />

      <h2>Numbers</h2>
    {personsToShow.map((person, index) => <Persons  deleteName={deleteName} key={index} index={index} person={person}/>)}

    </div>
  )
}

export default App
