import React from 'react'


const Notification = ({ error, notification }) => {

    const errorStyle = {
      color: 'red',
      background: 'lightgrey',
      fontSize: 20,
      borderStyle: 'solid',
      borderRadius: 5,
      padding: 10,
      marginBottom: 10
    }
    const notificationStyle = {
        color: 'green',
        background: 'lightgrey',
        fontSize: 20,
        borderStyle: 'solid',
        borderRadius: 5,
        padding: 10,
        marginBottom: 10
      }

    if (notification === null) {
      return null
    }

    if (error) {
      return (
        <div style={errorStyle} className="notification">
          {notification}
        </div>
      )
    }
    if (!error) {
      return (
        <div style={notificationStyle}className="notification">
          {notification}
        </div>
      )
    }



}

export default Notification
